'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'

interface Mosque {
  _id: string
  name: string
  address: string
  location: { lat: number; lng: number }
  timezone: string
  salatTimes: {
    fajr: { azan: string; jamat: string }
    dhuhr: { azan: string; jamat: string }
    asr: { azan: string; jamat: string }
    maghrib: { azan: string; jamat: string }
    isha: { azan: string; jamat: string }
  }
}

export default function HomePage() {
  const [mosques, setMosques] = useState<Mosque[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetchMosques()
  }, [])

  const fetchMosques = async () => {
    try {
      const response = await fetch('/api/mosques')
      if (response.ok) {
        const data = await response.json()
        setMosques(data)
      }
    } catch (error) {
      console.error('Error fetching mosques:', error)
    } finally {
      setLoading(false)
    }
  }

  const formatTime = (time: string) => {
    return new Date(`2000-01-01T${time}`).toLocaleTimeString('en-US', {
      hour: 'numeric',
      minute: '2-digit',
      hour12: true
    })
  }

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center">
          <div className="animate-pulse">Loading mosques...</div>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-4xl font-bold text-gray-900 mb-2">Salat-Time</h1>
          <p className="text-gray-600">Find prayer times at nearby mosques</p>
        </div>
        <div className="flex gap-4">
          <Link href="/admin/login">
            <Button variant="outline">Admin Login</Button>
          </Link>
        </div>
      </div>

      {/* Mosques List */}
      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {mosques.length === 0 ? (
          <div className="col-span-full text-center py-12">
            <p className="text-gray-500 text-lg">No mosques found</p>
            <p className="text-gray-400 mt-2">Check back later or contact an admin to add mosques</p>
          </div>
        ) : (
          mosques.map((mosque) => (
            <Card key={mosque._id} className="hover:shadow-lg transition-shadow">
              <CardHeader>
                <CardTitle className="text-xl">{mosque.name}</CardTitle>
                <p className="text-gray-600 text-sm">{mosque.address}</p>
                <Badge variant="secondary" className="w-fit">
                  {mosque.timezone}
                </Badge>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  <h4 className="font-semibold text-gray-900 mb-3">Today's Prayer Times</h4>
                  
                  {Object.entries(mosque.salatTimes).map(([prayer, times]) => (
                    <div key={prayer} className="flex justify-between items-center py-2 border-b border-gray-100 last:border-b-0">
                      <span className="font-medium capitalize text-gray-700">
                        {prayer}
                      </span>
                      <div className="text-right">
                        <div className="text-sm text-gray-600">
                          Azan: <span className="font-medium">{formatTime(times.azan)}</span>
                        </div>
                        <div className="text-sm text-gray-600">
                          Jamat: <span className="font-medium">{formatTime(times.jamat)}</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
                
                <div className="mt-4 pt-4 border-t">
                  <Button className="w-full" variant="outline">
                    Subscribe to Notifications
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))
        )}
      </div>
    </div>
  )
}
